@media (max-width: 1200px) {
    .menu {
        &_item {
            padding-right: 20px; }
        &_link {
            font-size: 13px; } }
    .promo {
        &_header {
            font-size: 50px; } }
    .reasons {
        &_block {
            align-items: flex-start; }
        &_descr {
            width: 300px; } }
    .mobile {
        &_item {
            width: 300px;
            height: 270px; } } }
@media (max-width: 991px) {
    .menu {
        &_item {
            padding-right: 8px; }
        &_link {
            font-size: 10px; } }
    .subheader {
        &_logo {
            width: 100%; }
        &_official {
            font-size: 11px; }
        &_phone {
            font-size: 20px; }
        &_btn {
            margin-top: 21px;
            width: 100%;
            height: 40px; } }
    .promo {
        &_header {
            font-size: 40px; }
        &_subheader {
            font-size: 30px; } }
    .reasons {
        &_block {
            flex-direction: column;
            align-items: center; }
        &_round {
            margin-right: 0; }
        &_descr {
            width: 300px;
            margin-top: 20px; } }
    .mobile {
        &_item {
            width: 232px;
            height: 270px;
            &_subtitle {
                font-size: 22px; }
            &_plus {
                left: 45%; } } }
    .last {
        &_link {
            left: 41%; } }
    .footer {
        &_link {
            width: 350px;
            margin: 0 -14px; }
        &_mobile {
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 131px; } } }
@media (min-width: 768px) {
    .xs_visible {
        display: none; } }

@media (max-width: 767px) {
    .menu {
        position: fixed;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        top: 0;
        left: -100%;
        width: 50%;
        height: 100vh;
        padding: 20px 15px;
        background-color: rgba(0, 0, 0, .8);
        transition: 0.6s all;
        &_active {
            left: 0; }
        .menu {
            &_item {
                padding-right: 0;
                bottom: none;
                margin: 10px 0; }
            &_link {
                font-size: 20px; } } }
    .opportunities {
        &_img {
            width: 100%; } }
    .hamburger {
        display: block;
        position: absolute;
        right: 10px;
        top: 18px;
        width: 26px;
        height: 12px;
        span {
            display: block;
            height: 2px;
            width: 100%;
            background-color: #fff;
            margin-bottom: 2px;
            transition: 0.5s all; }
        &_active {
            span {
                &:nth-child(1) {
                    transform:  translateY(3px) rotate(-45deg); }
                &:nth-child(2) {
                    display: none; }
                &:nth-child(3) {
                    transform: translateY(0px) rotate(45deg); } } } }
    .xs_hidden {
        display: none; }
    .xs_visible {
        display: block; }
    .subheader {
        &_phone {
            display: block;
            margin-top: 10px;
            font-size: 16px;
            text-align: center;
            color: #ffffff;
            font-weight: 400; }
        &_btn {
            display: block;
            width: 200px;
            height: 40px;
            margin: 0 auto;
            margin-top: 10px;
            &:hover {
                background-image: linear-gradient (to top, $main-color 0%, #4ce2ff 100%); } } }
    .promo {
        &_header {
            line-height: 45px; } } }
@media (max-width: 575px) {
    nav {
        height: 30px; }
    header {
        height: 30px; }
    .subheader {
        &_official {
            display: none; } }

    .subheader {
        height: 60px;
        top: 30px;
        .subheader {
            &_phone {
                font-weight: 400;
                font-size: 12px;
                text-transform: uppercase;
                margin-top: 11px; }
            &_btn {
                width: 110px;
                height: 22px;
                font-size: 10px;
                margin-top: 6px;
                &:hover {}
                background-image: linear-gradient (to top, $main-color 0%, #4ce2ff 100%); }
            &_logo {
                margin-top: 16px;
                img {
                    width: 130px; } } } }
    .promo {
        min-height: 400px;
        padding: 93px 0 35px 0;
        &_descr {
            display: none; }
        &_header {
            font-size: 30px;
            padding: 0 24px;
            line-height: 35px; }
        &_subheader {
            font-size: 20px;
            margin-top: 33px;
            padding: 0 10px;
            line-height: 30px; }
        &_btn {
            width: 210px;
            height: 50px;
            margin-top: 72px;
            &:hover {
                background-image: linear-gradient (to top, $main-color 0%, #4ce2ff 100%); } } }
    .menu {
        width: 85%;
        &_item {
            width: 100%;
            text-align: center;
            padding: 5px 0; }
        &_link {
            display: block; } }
    .reasons {
        padding: 22px 0 36px 0;
        &_block {
            margin-top: 26px; }
        &_img {
            width: 100px;
            height: 100px; }
        &_descr {
            width: 100%;
            margin-top: 27px; }
        &_subtitle {
            font-size: 16px; } }
    .title {
        font-size: 15px;
        margin-top: 15px;
        line-height: 16px; }
    .hamburger {
        top: 10px; }
    .last {
        &_link {
            left: 25%; } }
    footer {
	    padding: 25px 0 20px 0 {
        min-height: 245px; } }
    .footer {
        &_divider {
            width: 253px;
            margin: 0 auto; }

        &_wrapper {
            padding-top: 22px;
            flex-wrap: wrap; }

        &_wrapper > div {
            margin-bottom: 25px;
            width: 100%;

            &:nth-child(1) {
                order: 2; }
            &:nth-child(2) {
                order: 1; }
            &:nth-child(3) {
                order: 3;
                justify-content: space-around; } }
        &_social {
            flex-wrap: wrap;

            margin: 0 auto; }


        &_mobile {
            width: 260px;
            flex-direction: inherit; }




        &_link {
            width: 290px;
            flex-wrap: wrap;
            justify-content: center;
            margin: 0 auto;

            &_main {
                a {
                    padding: 0;
                    font-size: 14px; } }
            &_sub {
                flex-wrap: wrap;
                justify-content: center;
                a {
                    font-size: 11px;
                    margin: 0 7px; } }
            &_lang {
                margin-top: 10px;
                font-size: 11px; } } } }

