
header {
	position: relative;
	height: 50px; }

nav {
	position: fixed;
	width: 100%;
	height: 50px;
	background-color: #212121;
	z-index: 2; }
.hamburger {
	display: none; }
.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style-type: none;

	&_link {
		color: #ffffff;
		font-size: 15px;
		font-weight: 300; }

	&_item {
		border-right: 2px dotted black;
		padding-right: 28px;
		&:last-child {
				border-right: none; } } }

.subheader {
	position: absolute;
	top: 50px;
	width: 100%;
	height: 84px;
	background-color: rgba(0, 0, 0, 0.6);

	&_logo {
		display: block;
		max-width: 100%;
		margin-top: 16px; }

	&_official {
		margin-top: 6px;
		opacity: 0.5;
		color: #ffffff;
		font-size: 13px;
		font-weight: 400; }

	&_call {
		color: #ffffff;
		font-size: 12px;
		font-weight: 300;
		margin-top: 19px;
		text-align: right; }

	&_phone {
		display: block;
		margin-top: 8px;
		color: #ffffff;
		font-size: 22px;
		font-weight: 400;
		text-transform: uppercase;
		text-align: right;
		line-height: 12px; }

	&_btn {
		margin-top: 19px;
		width: 172px;
		height: 46px;
		box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: #ffffff;
		font-size: 14px;
		font-weight: 400;
		text-transform: uppercase;
		@include btn_hover; } }


.promo {
	min-height: 800px;
	background: url('../img/main-img.jpg') center (center / cover) no-repeat;
	padding: 150px 0 135px 0;

	&_header {
		color: #ffffff;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 60px;
		line-height: 60px;
		margin: 0;
		text-align: center; }

	&_subheader {
		color: #ffffff;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 36px;
		line-height: 36px;
		margin: 0;
		margin-top: 22px;
		text-align: center; }

	&_descr {
		color: #ffffff;
		font-size: 18px;
		font-weight: 400;
		line-height: 24px;
		text-align: center;
		margin-top: 23px; }

	&_btn {
		display: block;
		margin: 118px auto 0 auto;
		width: 259px;
		height: 63px;
		box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: #ffffff;
		font-size: 18px;
		font-weight: 400;
		text-transform: uppercase;
		@include btn_hover; } }

.reasons {
		padding: 71px 0 65px 0;
		&_block {
				display: flex;
				justify-content: center;
				align-items: center;
				margin-top: 35px; }
		&_img {
				display: flex;
				justify-content: center;
				align-items: center;
				margin-right: 26px;
				width: 113px;
				height: 113px;
				background-color: #1eacc7;
				border-radius: 100%; }

		&_descr {
				width: 343px; }

		&_subtitle {
			line-height: 21px;
			color: #1eacc7;
			font-size: 17px;
			font-weight: 400; }

		&_text {
			color: #202020;
			font-size: 15px;
			font-weight: 300;
			line-height: 21px; } }


.label {
	margin: 0 auto;
	height: 24px;
	width: 112px;
	padding: 5px 12px;
	background-color: #000000;
	color: #ffffff;
	font-size: 11px;
	font-weight: 300;
	text-transform: uppercase;
	text-align: center;
	&_white {
		background-color: #ffffff;
		color: #000000; } }

.title {
	margin-bottom: 0;
	margin-top: 38px;
	color: #222222;
	font-size: 38px;
	font-weight: 700;
	text-transform: uppercase;
	text-align: center;
	&_opportunities {
			font-size: 38px;
			font-weight: bold;
			text-align: center;
			color: #ffffff;
			text-transform: uppercase;
			z-index: 3; }
	&_last {
		margin-bottom: 0;
		margin-top: 30px;
		color: #ffffff;
		font-size: 38px;
		font-weight: 700;
		text-transform: uppercase;
		text-align: center; } }
.subtitle {
	margin-top: 19px;
	font-size: 17px;
	font-weight: normal;
	text-align: center;
	color: #222222;
	&_last {
		margin-top: 10px;
		font-size: 17px;
		font-weight: normal;
		text-align: center;
		color: #fff; } }

.mobile {
		min-height: 864px;
		padding: 48px 0 79px 0;
		background: url('../img/mobile_background.png') center (center / cover) no-repeat;
		.row {
				margin-top: 32px; }
		&_item {
				position: relative;
				width: 320px;
				height: 280px;
				margin: 0 auto;
				margin-bottom: 19px;
				padding-top: 92px;
				&_1 {
						background: url('../img/mobile_apps_img_1.png') center (center / cover) no-repeat; }
				&_2 {
						background: url('../img/mobile_apps_img_2.png') center (center / cover) no-repeat; }
				&_3 {
						background: url('../img/mobile_apps_img_3.png') center (center / cover) no-repeat; }
				&_4 {
						background: url('../img/mobile_apps_img_4.png') center (center / cover) no-repeat; }
				&_5 {
						background: url('../img/mobile_apps_img_5.png') center (center / cover) no-repeat; }
				&_6 {
						background: url('../img/mobile_apps_img_6.png') center (center / cover) no-repeat; }
				&_subtitle {
						font-size: 24px;
						font-weight: normal;
						text-align: center;
						color: #ffffff; }
				&_plus {
						display: flex;
						justify-content: center;
						align-items: center;
						position: absolute;
						bottom: 35px;
						left: 50%;
						transform: trandlsteX(-50%);
						width: 26px;
						height: 26px;
						background-color: #000000;
						margin: 0 auto;
						border-radius: 100%;
						padding: 6px;
						cursor: pointer;
						&:before {
							content: '';
							display: block;
							width: 14px;
							height: 2px;
							background-color: $main-color;
							transition: 0.3s all; }
						&::after {
							content: '';
							position: absolute;
							left: 50%;
							transform: translateX(-50%);
							display: block;
							height: 14px;
							width: 2px;
							background-color: $main-color;
							transition: 0.3s all; }
						&:hover {
							&:before, &::after {
									background-color: $second-color; } } } } }

.opportunities {
		position: relative;
		min-height: 895px;
		padding: 57px 0 138px 0;
		background: url('../img/mobile_background.png') center (center / cover) no-repeat;
		.title_opportunities {
			font-size: 38px;
			font-weight: bold;
			text-align: center;
			color: #ffffff;
			text-transform: uppercase;
			z-index: 3; }
		.subtitle_opportunities {
			font-size: 17px;
			font-weight: normal;
			text-align: center;
			color: #ffffff;
			z-index: 4; }
		&_img {
			position: relative;
			display: block;
			margin: 56px auto 0 auto;
			z-index: 3; }
		.black {
			position: absolute;
			top: 0;
			left: 0;
			height: 499px;
			width: 100%;
			background-color: #070716;
			z-index: 1; }
		&_descr {
			font-size: 17px;
			font-weight: 300;
			line-height: 1.41;
			text-align: center;
			color: #222222; } }

.require {
	padding: 68px 0 84px 0;
	&_block {
		margin: 33px 0;
		&_nmb {
			margin-bottom:  0px; } }
	&_title {
		font-size: 22px;
		color: #1ca8c3; }
	&_list {
		list-style-type: none;
		padding-left: 50px;
		margin-top: 22px; }
	li {
		position: relative;
		font-size: 17px;
		font-weight: 300;
		line-height: 24px;
		margin-bottom: 10px;
		&:before {
			content: '';
			display: block;
			position: absolute;
			left: -32px;
			top: 3px;
			width: 21px;
			height: 21px;
			background: url('../icons/check_blu.png')  center (center / cover) no-repeat; } }
	.warning {
		.require_title {
			color: $second-color; }
		.require_list {
			li {
				&:before {
					background: url('../icons/check_red.png')  center (center / cover) no-repeat; } } } } }

.last {
	min-height: 717px;
	position: relative;
	padding: 49px 0;
	background: url('../img/world_map.png') center (center / cover) no-repeat;
	.label_last {
		width: 76px;
		height: 24px;
		background-color: #ffffff;
		margin: 0 auto;
		padding: 5px 12px;
		font-size: 11px;
		font-weight: 300;
		text-transform: uppercase;
		text-align: center;
		color: #000000; }
	&_link {
			position: absolute;
			left: 44%;
			transform: translateY(-50%);
			color: #1fbad6;
			text-align: center;
			bottom: 49px;
			margin: 0 auto; } }

footer {
	padding: 14px 23px 39px 23px;
	a {
		&:hover {
			text-decoration: none; } } }

.footer {
	&_divider {
		width: 299px;
		height: 4px;
		background-color: #1fbad6;
		margin: 0 auto; }
	&_wrapper {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		padding-top: 32px; }
	&_wrapper>div {
		width: 33%;
		&:nth-child(3) {
			display: flex;
			justify-content: flex-end; } }
	&_social {
		width: 160px;
		display: flex;
		justify-content: space-between;
		&_item {
			display: flex;
			justify-content: center;
			align-items: center;
			margin: 0 4px;
			width: 32px;
			height: 32px;
			border-radius: 100%;
			background-color: #070716;
			i {
				color: #ffffff; } } }
	&_link {
		width: 390px;
		margin: 0 auto;
		&_main {
			display: flex;
			justify-content: space-between;
			padding: 0 10px;
			a {
				font-size: 15px;
				font-weight: 300;
				text-transform: uppercase;
				color: #070716; } }
		&_sub {
			display: flex;
			justify-content: space-between;
			margin-top: 10px;
			a {
					font-size: 11px;
					font-weight: 300;
					text-transform: uppercase;
					color: #999999; } }
		&_lang {
			display: flex;
			justify-content: center;
			padding-top: 20px;
			font-size: 11px;
			font-weight: normal;
			text-transform: uppercase;
			color: #1fbad6; } }
	&_mobile {
		margin-right: 44px;
		width: 297px; } }

